                option1:{
                    title : {
                        text: '企业需求增长统计',
                        x:'center'
                    },
                    xAxis: {
                        type: 'category',
                        data: ''
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: '',
                        type: 'line'
                    }]
                },
                info:'',
                HotDemonds:'',
                imgList:[
                    {
                        id: 0,
                        imgUrl: li1
                    },
                    {
                        id: 1,
