                imgList1:[
                    {
                        id: 4,
                        imgUrl: li5
                    },{
                        id: 5,
                        imgUrl: li6
                    },
                    {
                        id: 6,
                        imgUrl: li7
                    },
                    {
                        id: 7,
                        imgUrl: li8
                    },
                ]
            }
        },
        created(){
            this.getInfo();
            this.getHotDemonds();
        },
        mounted(){
            this.getPieInfo();
            this.getCharts1();

        },
        methods:{
            //去申报
            toForm() {
                var that = this;
                var status = sessionStorage.getItem('status');
                console.log('status')
                console.log(status)
                if (status == 1) {
                    this.$router.push('/Home/NeedRelease')
                } else {
                    this.$confirm('未进行企业认证，是否现在完成认证', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.$router.push('/enterpriseCenter')
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消'
                        });
                    });
                }
            },
            toExpert(){
                this.$alert('功能开发中','提醒', {
                    confirmButtonText: '确定',
                });
            },
            toSearch(val){
                console.log(val)
                this.$store.state.demandIndustry = val;
                this.$router.push('/Home/NeedHome')
            },
            //初始化e-charts
            initCharts () {
                let myChart = this.$echarts.init(this.$refs.chart);
                console.log(this.$refs.chart)
                // 绘制图表
                myChart.setOption(this.option);
            },
            //初始化e-charts1
            initCharts1 () {
                let myChart = this.$echarts.init(this.$refs.chart1);
                console.log(this.$refs.chart1)
                // 绘制图表
                myChart.setOption(this.option1);
            },
            //获取饼图数据
            getPieInfo(){
                var that = this;
                console.log('获取饼图数据')
                this.$axios.get('/demands/demandsIndustryProp')
                    .then((res)=>{
                        if (res.data.code==0){
                            var arr = [];
                            res.data.data.sdata.forEach((item,key)=>{
                                arr.push({value:item.value,name:item.name})
                            })
                            console.log(arr)
                            that.option.series[0].data = arr;
                            that.initCharts();
                        }
                        console.log(res)
                    })
            },
            //获取折线图数据
            getCharts1(){
                var that = this;
                this.$axios.get('/demands/demandsRiseTrend')
                    .then((res)=>{
                        if (res.data.code==0){
                            that.option1.series[0].data = res.data.data.sdata;
                            that.option1.xAxis.data = res.data.data.xdata;
                        }
                        console.log(res);
                        that.initCharts1();
                    })
            },
            //获取最新新闻
            getInfo(){
                var that = this;
                this.$axios.get('/demands/lastDemandsShow')
                    .then(res=>{
                        console.log('最新新闻');
                        if (res.data.code==0){
                            that.info = res.data.data
                        }
                        console.log(res)
                    })
            },
            //获取热门板块
            getHotDemonds(){
                var that = this;
                this.$axios.get('/demands/hotDemandsIndustry')
                    .then(res=>{
                        console.log('获取热门板块')
                        if (res.data.code==0){
                            that.HotDemonds = res.data.data.slice(0,4);
                            that.HotDemonds1  = res.data.data.slice(4,8);
                            console.log(that.HotDemonds1)
                           //   res.data.data.forEach((item,key)=>{
                          //       console.log(item)
                          //
                          //   })
                            console.log(res.data.data);
                        }
                        console.log(res)
                    })
            }

        }
    }
</script>

